import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

/**
 * Note: 
 *
 * hidden: true                   //  true  401login/edit/1
 * alwaysShow: true               //  children 1--
 *                                // --
 *                                //  children 
 *                                //  alwaysShow: true
 * redirect: noRedirect           //  noRedirect 
 * name:'router-name'             // <keep-alive>
 * meta : {
    roles: ['admin','editor']    // 
    title: 'title'               // 
    icon: 'svg-name'             // src/icons/svg
    breadcrumb: false            // falsebreadcrumb
  }
 */

const _import = require('./_import_')//

// 
export const constantRouterMap = [
  {
      path: '/console',
      component: (resolve) => require(['../views/console'], resolve),
      hidden: true
  },
]

var MyRouter = new Router({
  // mode: 'history', //
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

export default MyRouter



